*{
  padding:0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height: 100vh;
}

.navbar{
  background-color: lightgreen !important;
}

.login-form{
  background-color: lightblue;
  border: 2px 3px 3px 3px;
  border-color: black;
}

.login-items{
  margin-left: 10cap;
  margin-right: 10cap;
}

.search-bar{
  width:40cap;
}


.register-form{
  margin-left: 10cap;
}

.login-page{
  background-color: aliceblue;
  border-radius: 2cap;
  display:flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.register-page{
  background-color: aliceblue;
  display:flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.h1-text{
  margin-left: 1cap;
  margin-right: 1cap;
}

.register-link{
  margin-left: 1cap;
}
.btn {
  margin-bottom: 1cap;
  margin-right: 1cap;
}

.filters{
  display:flex;
  align-items: center;
  justify-content: space-between;
  padding:15px 20px;
  box-shadow: 0 0 3px gray;
}

/*antd icons*/

.switch-icons{
  border:1px solid  rgba(0, 0, 0, 0.673);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color:black;
}

.inactive-icon{
  color:gray;
}


